  font-family: "rubikSemiBold";
  src: url("../fonts/Rubik-SemiBold.ttf") format("truetype");
  /* font-weight: normal;
  font-style: normal; */
}
.header {
  background-color: #fff;
  height: 80px;
  width: 100%;
  padding: 1.5em 4em;
}
.header .content-header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100%;
  gap: 30em;
}
@media screen and (max-width: 1480px) {
  .header .content-header {
    gap: 10em;
  }
}
@media screen and (max-width: 1100px) {
  .header .content-header {
    gap: 0;
    justify-content: space-between;
  }
}

.menu-header .menus {
  display: flex;
  justify-content: center;
  gap: 2em;
  align-items: center;
}
@media screen and (max-width: 1100px) {
  .menu-header .menus {
    display: none;
  }
}
.menu-header .menus a {
  text-align: center;
  color: #222831;
  font-family: "rubikRegular", "Courier New", Courier, monospace;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  font-weight: 600;
}
.menu-header .navbar {
  position: relative;
  padding: 1rem;
  display: none;
}
.menu-header .navbar .menu-toggle {
  cursor: pointer;
}
.menu-header .navbar .menu-toggle .bar {
  display: block;
  width: 35px;
  height: 5px;
  background-color: #222831;
  margin: 5px auto;
  transition: 0.3s;
  border-radius: 10px;
}
@media (max-width: 1100px) {
  .menu-header .navbar {
    display: block;
  }
}

.header-menu-mobile {
  background-color: #222831;
}
.header-menu-mobile .menus-mobile {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 1em 0;
  width: 100%;
}
.header-menu-mobile .menus-mobile a {
  text-align: center;
  color: #EEEEEE;
  width: 100%;
  border-bottom: 1px solid #EEEEEE;
  padding: 1.1em 0;
}
.header-menu-mobile .menus-mobile a:first-child {
  padding-top: 0;
}
.header-menu-mobile .menus-mobile a:last-child {
  border-bottom: none;
  padding: 0;
  padding-top: 1.1em;
}

main {
  min-height: 650px;
}

.container {
  max-width: 1280px;
  width: 100%;
  margin: 1rem auto;
}

.banner {
  width: 100%;
  height: 550px;
  background-image: url("../assets/images/obragrande.webp");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
@media screen and (max-width: 500px) {
  .banner {
    background-position: right;
  }
}

.banner-text {
  position: absolute;
  top: 120px;
  left: 100px;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 1rem;
  border-radius: 2px;
}
.banner-text h2 {
  font-size: 50px;
  color: #EEEEEE;
  font-weight: bold;
}
.banner-text h5 {
  color: #3474a7;
  font-size: 30px;
  font-weight: bold;
  margin-top: 0.6rem;
  font-family: "rubikBold", "Courier New", Courier, monospace;
  text-shadow: -1px -1px 0 white, 1px -1px 0 white, -1px 1px 0 white, 1px 1px 0 white;
}
@media screen and (max-width: 710px) {
  .banner-text {
    left: 0px;
    width: 90%;
    position: relative;
    margin: 0 auto;
    border-radius: 2px;
  }
}

#whatsapp {
  position: fixed;
  top: 85%;
  right: 2%;
}

.introduction {
  padding-bottom: 2rem;
}
.introduction h1 {
  font-size: 33px;
  padding: 0.7rem 0;
  font-family: "rubikSemiBold", "Courier New", Courier, monospace;
  margin: 0 auto;
  line-height: 2.3rem;
  color: #3474a7;
}
@media screen and (max-width: 1000px) {
  .introduction {
    text-align: center;
  }
}

.content-introduction {
  display: flex;
  justify-content: center;
  gap: 4rem;
  align-items: center;
  padding: 2rem 0 0 0;
}
@media screen and (max-width: 1000px) {
  .content-introduction {
    flex-wrap: wrap;
    padding: 0 0.5rem 0.5rem;
  }
}

.introduction-img {
  width: 450px;
  height: 450px;
}
.introduction-img img {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.6);
}
@media screen and (max-width: 450px) {
  .introduction-img {
    height: 350px;
    width: 350px;
  }
}
@media screen and (max-width: 350px) {
  .introduction-img {
    height: 250px;
    width: 250px;
  }
}

.introduction-text {
  width: 100%;
  height: 100%;
  align-self: self-start;
  max-width: 45%;
  font-size: 18px;
  line-height: 1.7rem;
  font-family: "rubikRegular", "Courier New", Courier, monospace;
}
@media screen and (max-width: 1000px) {
  .introduction-text {
    max-width: 100%;
  }
}
.introduction-text button {
  padding: 0.8rem 4rem;
  color: #EEEEEE;
  font-size: 1.1rem;
  margin-top: 3rem;
  background-color: rgba(3, 103, 82, 0.8);
  border: none;
  cursor: pointer;
  font-weight: 700;
  border-radius: 5px;
  font-family: "rubikRegular", "Courier New", Courier, monospace;
}
.introduction-text p {
  padding-top: 0.5rem;
  font-family: "rubikRegular", "Courier New", Courier, monospace;
  text-align: justify;
}
.introduction-text .data-build {
  padding: 0.7rem;
  background-color: rgba(45, 104, 147, 0.8);
  color: #EEEEEE;
  margin-top: 1rem;
}
.introduction-text .data-build p {
  font-family: "rubikLight", "Courier New", Courier, monospace;
}
.introduction-text .data-build p span {
  font-family: "rubikBold", "Courier New", Courier, monospace;
}

/* second section */
.help {
  width: 100%;
  margin: 2rem 0;
}
.help h2 {
  font-size: 30px;
  font-family: "rubikRegular", "Courier New", Courier, monospace;
  background-color: #3474a7;
  color: #EEEEEE;
  padding: 0.9rem;
  text-align: center;
  margin: 0 auto;
  max-width: 800px;
  margin-bottom: 2rem;
}
@media screen and (max-width: 800px) {
  .help h2 {
    width: 90%;
  }
}
.help .content-help {
  padding: 1rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4rem;
  width: 100%;
}
.help .content-help .content-help-text {
  width: 100%;
  height: 100%;
  align-self: flex-start;
}
.help .content-help .content-help-text .help-text-list ul {
  display: flex;
  justify-content: space-around;
  gap: 2rem;
  flex-wrap: wrap;
}
.help .content-help .content-help-text .help-text-list ul li {
  background-color: rgba(246, 242, 242, 0.6);
  width: auto;
  width: 300px;
  min-height: 100px;
  padding: 1rem;
  text-align: center;
  border-radius: 5px;
  gap: 0.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  font-family: "rubikSemiBold", "Courier New", Courier, monospace;
}
@media screen and (max-width: 300px) {
  .help .content-help .content-help-text .help-text-list ul li {
    width: 90%;
  }
}
.help .content-help .content-help-text .help-text-list ul li a {
  width: 100%;
  margin-top: 1rem;
}
.help .content-help .content-help-text .help-text-list ul li a div {
  width: auto;
  width: 100%;
  display: flex;
  justify-content: center;
}
.help .content-help .content-help-text .help-text-list ul li a div span {
  background-color: rgba(3, 103, 82, 0.8);
  padding: 0.2rem 0.5rem;
  color: #EEEEEE;
  border-radius: 2px;
}

/*  third section - service */
.content-service h2 {
  font-size: 30px;
  text-align: center;
  padding: 0.7rem 0;
  font-family: "rubikRegular", "Courier New", Courier, monospace;
  background-color: #3474a7;
  color: #EEEEEE;
  max-width: 800px;
  margin: 0 auto;
}
@media screen and (max-width: 800px) {
  .content-service h2 {
    width: 90%;
  }
}
.content-service .flex-content-service {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem 0;
  flex-wrap: wrap;
  gap: 4rem;
}
.content-service .card-service {
  width: 350px;
  height: 390px;
  box-shadow: 10px 10px 6px 0px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 5px;
}
@media screen and (max-width: 350px) {
  .content-service .card-service {
    width: 90%;
    height: auto;
  }
}
.content-service .card-service .card-service-img img {
  width: 348px;
  height: 200px;
}
@media screen and (max-width: 350px) {
  .content-service .card-service .card-service-img img {
    width: 90%;
    height: auto;
  }
}
.content-service .card-service .card-service-content {
  width: 100%;
  height: 100%;
  padding: 1rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: 1.5rem;
}
.content-service .card-service .card-service-content h3 {
  font-size: 20px;
  text-align: center;
  font-family: "rubikMedium", "Courier New", Courier, monospace;
}
.content-service .card-service .card-service-content p {
  font-size: 16px;
  font-family: "rubikRegular", "Courier New", Courier, monospace;
  justify-self: start;
}
.content-service .card-service .card-link {
  display: flex;
  justify-content: flex-end;
  padding: 0.7rem;
}
.content-service .card-service .card-link a {
  color: #2d6893;
  text-decoration: underline;
  font-size: 18px;
  font-weight: 500;
}

.content-questions h3 {
  font-size: 30px;
  text-align: center;
  padding: 0.7rem 0;
  font-family: "rubikRegular", "Courier New", Courier, monospace;
  background-color: #3474a7;
  color: #EEEEEE;
  max-width: 800px;
  margin: 0 auto;
}
@media screen and (max-width: 800px) {
  .content-questions h3 {
    width: 90%;
  }
}
.content-questions .topic-invisible {
  display: none;
}
.content-questions .questions {
  display: flex;
  justify-content: center;
  gap: 2rem;
  align-items: center;
  padding: 2rem 0 1rem 0;
}
@media screen and (max-width: 1000px) {
  .content-questions .questions {
    flex-direction: column-reverse;
    width: 100%;
    align-items: center;
  }
}
.content-questions .questions-img {
  width: 450px;
  height: 450px;
  align-self: flex-start;
}
.content-questions .questions-img img {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.6);
}
@media screen and (max-width: 1000px) {
  .content-questions .questions-img {
    align-self: center;
  }
}
@media screen and (max-width: 450px) {
  .content-questions .questions-img {
    height: 350px;
    width: 350px;
  }
}
@media screen and (max-width: 350px) {
  .content-questions .questions-img {
    height: 250px;
    width: 250px;
  }
}
.content-questions .answers {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 45%;
  align-self: flex-start;
}
@media screen and (max-width: 1000px) {
  .content-questions .answers {
    align-self: center;
    width: 90%;
  }
}
.content-questions .answers .topic-answers {
  background-color: rgba(45, 104, 147, 0.8);
  color: #EEEEEE;
  padding: 1rem;
  font-family: "rubikRegular", "Courier New", Courier, monospace;
  display: flex;
  gap: 1rem;
  align-items: center;
  border-radius: 2px;
  justify-content: space-between;
  cursor: pointer;
}
.content-questions .answers .answer-topic {
  padding: 1rem;
  background-color: rgba(45, 104, 147, 0.9);
  margin-top: 0.3rem;
  border-radius: 2px;
  color: #EEEEEE;
  font-family: "rubikMedium", "Courier New", Courier, monospace;
}

.footer {
  background-color: #393E36;
  width: 100%;
}

.content-footer {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  padding: 1.5rem 7rem;
}
@media screen and (max-width: 1400px) {
  .content-footer {
    padding: 1.5rem 3rem;
  }
}
@media screen and (max-width: 1100px) {
  .content-footer {
    flex-direction: column;
    justify-content: center;
    gap: 2rem;
  }
}

.contatos, .localizacao {
  width: 100%;
  height: 100%;
  justify-content: start;
  align-items: flex-start;
}
.contatos p, .localizacao p {
  font-size: 24px;
  color: #EEEEEE;
  margin-bottom: 1.5rem;
  font-weight: 700;
  text-decoration: underline;
  font-family: "rubikRegular", "Courier New", Courier, monospace;
}
.contatos ul, .localizacao ul {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  justify-content: start;
  align-items: flex-start;
  color: #EEEEEE;
  font-size: 14px;
  font-family: "rubikRegular", "Courier New", Courier, monospace;
}
.contatos ul li span, .localizacao ul li span {
  font-size: 20px;
  font-weight: bold;
}

.sociais p {
  font-size: 24px;
  color: #EEEEEE;
  margin-bottom: 1.5rem;
  font-weight: 700;
  text-decoration: underline;
  font-family: "rubikRegular", "Courier New", Courier, monospace;
}
.sociais ul {
  display: flex;
  gap: 2rem;
}